<template>
  <div class="chat-list" ref="chat">
    <chat-item :comment="item" :isMe="item.user.id == myId" v-for="item in chatList" :key="item.id" />
  </div>
</template>
<script>
import axios from 'axios'
import Cookies from '@/utils/Cookies'
import chatItem from './chat-item'
export default {
  components: {
    chatItem,
  },
  data() {
    return {
      chatList: [],
      lastCommentTime: '',
      myId: 0,
      intervalId: 0,
    }
  },
  mounted() {
    if (Cookies.get('user')) {
      this.myId = Cookies.get('user').id
    }
    this.getOld()
    this.intervalId = setInterval(() => {
      this.refresh()
    }, 30 * 1000)
  },

  methods: {
    toBottom() {
      this.$refs.chat.scrollTop = 9999999999
    },
    getOld() {
      axios.get(`https://gitee.com/api/v5/repos/ceemo/giteechat/issues/I1OWHA/comments?access_token=${Cookies.get('token')}&page=1&per_page=40&order=desc`).then((res) => {
        if (res.status == 200 && res.data && res.data.length) {
          this.lastCommentTime = res.data[0].created_at
          this.chatList = res.data.reverse()
          this.$nextTick(() => {
            this.toBottom()
          })
        }
      })
    },
    refresh() {
      axios.get(`https://gitee.com/api/v5/repos/ceemo/giteechat/issues/I1OWHA/comments?access_token=${Cookies.get('token')}&page=1&per_page=20&order=asc&since=${this.lastCommentTime}`).then((res) => {
        if (res.status == 200 && res.data && res.data.length) {
          this.lastCommentTime = res.data[res.data.length - 1].created_at
          this.chatList = this.chatList
          res.data.forEach((item) => {
            this.chatList.push(item)
          })
          this.$nextTick(() => {
            this.toBottom()
          })
        }
      })
    },
    clear() {
      this.chatList = []
    },
  },
  beforeDestroy() {
    clearInterval(this.intervalId)
  },
}
</script>
<style lang="less">
.chat-list {
  height: calc(100vh - 50px);
  overflow: auto;
  padding: 10px;
}
</style>
