<template>
  <div class="home">
    <chat-list ref="chat" />
    <div class="chat-input">
      <a-row>
        <a-col :span="22">
          <a-input-search ref="input" v-model="message" @search="sendMessage" :disabled="sending">
            <a-button slot="enterButton" type="primary" @click="sendMessage" :disabled="sending">send</a-button>
          </a-input-search>
        </a-col>
        <a-col :span="2" class="button">
          <a-button-group>
            <a-button type="primary" class="refresh" icon="reload" @click="refresh"> </a-button>
            <a-button type="primary" class="refresh" icon="file-sync" @click="clear"> </a-button>
            <a-button type="primary" class="refresh" icon="logout" @click="logout"> </a-button>
          </a-button-group>
        </a-col>
      </a-row>
    </div>
  </div>
</template>

<script>
import Cookies from '@/utils/Cookies'
import chatList from '@/components/chat-list'
import axios from 'axios'
export default {
  name: 'Home',
  components: {
    chatList,
  },
  data() {
    return {
      message: '',
      sending: false,
    }
  },

  methods: {
    logout() {
      Cookies.remove('user')
      Cookies.remove('token')
      this.$router.replace('/login')
    },
    refresh() {
      this.$refs.chat.refresh()
    },
    clear() {
      this.$refs.chat.clear()
    },
    sendMessage() {
      if (!this.sending) {
        this.sending = true
        let message = this.message
        axios
          .post(`https://gitee.com/api/v5/repos/ceemo/giteechat/issues/I1OWHA/comments`, {
            access_token: Cookies.get('token'),
            body: message,
          })
          .then((res) => {
            this.$refs.chat.refresh()
            this.message = ''
            this.sending = false
            this.$nextTick().then(() => {
              this.$refs.input.focus()
            })
          })
          .catch((e) => {
            this.$message.error('发送失败,请重试')
            this.sending = false
          })
      }
    },
  },
}
</script>
<style lang="less">
.home {
  height: 100vh;

  .chat-input {
    border-top: 1px solid #e9e9e9;
    padding: 10px;
    height: 50px;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
  }
  .button {
    padding-left: 5px;
  }
}
</style>
